import { createPoolDb, type PoolDatabase } from '@tempo-sync/db';
import { Hono } from 'hono';
import { cors } from 'hono/cors';
import { logger } from 'hono/logger';

type AppEnv = {
  Bindings: {
    DATABASE_URL: string;
  };
  Variables: {
    db: PoolDatabase;
  };
};

export const createApp = <T extends AppEnv>() => {
  const app = new Hono<T>();

  app.use(logger());
  app.use('*', cors());

  app.use('*', async (c, next) => {
    const db = createPoolDb(c.env.DATABASE_URL);
    c.set('db', db);
    await next();
  });

  app.get('/health', c => {
    return c.json({ status: 'ok', timestamp: Date.now() }, 200);
  });

  return app;
};
